.done {
  text-decoration: line-through;
}
html {
  height: 100%;
  width: 100%;
}
.navbar {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  margin-top: 0px;
  margin-left: 0px;
  width: 100%;
  height: 60px;
  background: #157;
  padding: 10px;
  margin-bottom: 40px;
  color: aliceblue;
  box-shadow: 0px 2px 8px #444;
}
body {
  background-image: linear-gradient(to right, #243949 0%, #517fa4 100%);
  background-repeat: no-repeat;
  height: 100%;
  background-attachment: fixed;
  min-width: 300px;
}
.form {
  min-width: 300px;
  border-radius: 3px;
  width: 100%;
  display: inline;
  height: 10px;
}
.inputbox {
  width: 130px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  width: 80%;
  min-width: 200px;
  background-color: white;
  background-repeat: no-repeat;
  padding: 12px 20px 12px 15px;
}

.addButtom {
  position: static;
  top: 3px;
  left: 3px;
  width: 15%;
  min-width: 50px;

  height: 30px;
  background-image: linear-gradient(to top, #9be15d 0%, #00e3ae 100%);
  border: none;
  border-radius: 3px;
  color: white;
  font-size: 10px;
}
.div {
  min-width: 300px;
  position: static;
  -webkit-transition: width 2s, height 4s; /* Safari */
  transition: width 2s, height 4s;
  padding: 10px;
  background-image: linear-gradient(-20deg, #00cdac44 0%, #8ddad544 100%);
  margin: 10px;
  border: none;
  border-radius: 30px;
  height: 50%;
  box-shadow: 0px 5px 10px #243949;
}
.allDone {
  width: 150px;
  height: 30px;
  border: none;
  border-radius: 3px;
  color: white;
  font-size: 20px;
  margin-left: 50px;
}
.listUI {
  background: white;
  box-shadow: 0ch 1px 5px #eee;
  border: none;
  border-radius: 50px;
  margin: 8px;
  width: auto;
  height: 40px;
  font-family: "Kalam", cursive;
  font-size: 1.3rem;
  padding: 3px;
}
.ListUIRemove {
  text-decoration: wavy;
  color: #333;
  background: #eee;
  box-shadow: 0ch 1px 5px #eee;
  border: none;
  border-radius: 50px;
  margin: 8px;
  width: auto;
  height: 40px;
  font-size: 1.3rem;

  font-family: "Kalam", cursive;
  text-decoration: line-through;
}
.removeButton {
  padding-inline-end: 5px;
  margin-right: 5px;
  height: 40px;
  float: right;
  border: none;
  border-radius: 30px;
  background: rgb(201, 47, 47);
  color: white;
}
input[type="checkbox"] {
  height: 20px;
  width: 18px;
}
